<template>
	<div>
		<CRow>
			<CCol sm="12">
				<CCard>
					<CCardHeader>
						<b>Order</b> Barang
					</CCardHeader>
					<CCardBody>
						<CForm class="mt-4">
							<CRow class="mx-auto d-flex justify-content-center">
                    			<CCol sm="8">
				                    <CSelect
				                    	placeholder="Pilih Barang"
				                        :label="labelBarang"
				                        horizontal
				                        v-model="rpo.nama_barang"
				                        :options="barang"
				                        @update:value="assignNamaBarang"
				                      />    
				                    <CTextarea
			                            label="Spesifikasi Barang"
			                            :description="validator.spesifikasi_barang_msg"
			                            :is-valid="validator.spesifikasi_barang"
			                            @input="rpo.spesifikasi_barang.length < 1 ? validator.spesifikasi_barang = false : validator.spesifikasi_barang = true"
			                            horizontal
			                            rows="3"
			                            v-model="rpo.spesifikasi_barang"
			                          />  

			                        <CInput
			                            type="number"
			                            :description="validator.qty_msg"
			                            :is-valid="validator.qty"
			                            autocomplete="qty"
			                            label="Quantity"
			                            horizontal
			                            placeholder="Masukan Quantity"
			                            v-model="rpo.qty"
			                          />  
			                        <CInput
			                        	type="number"  
			                        	:description="validator.harga_jual_msg"
			                        	:is-valid="validator.harga_jual"
			                        	autocomplete="harga_jual"
			                        	label="Harga Jual Satuan"
			                        	horizontal
			                        	placeholder="Masukan Harga Jual Satuan"
			                        	v-model="rpo.harga_jual"
			                          />
			                        <CInput
				                        type="number"
				                        :description="validator.tax_msg"
				                        :is-valid="validator.tax"
				                        @input="rpo.tax.length < 1 ? validator.tax = false : validator.tax = true"
				                        autocomplete="tax"
				                        label="Pajak (%)"
				                        horizontal
				                        placeholder="Masukan Tax Rate"
				                        v-model="rpo.tax_rate"
				                      />
				                      <CInput
				                        type="number"
				                        :description="validator.diskon_msg"
				                        :is-valid="validator.diskon"
				                        @input="rpo.diskon.length < 1 ? validator.diskon = false : validator.diskon = true"
				                        autocomplete="diskon"
				                        label="Diskon %"
				                        horizontal
				                        placeholder="Masukan Diskon"
				                        v-model="rpo.diskon"
				                      />  
                    			</CCol>
                    			<CCol sm="8" class="d-flex justify-content-end mt-4">
                    				<button class="btn btn-light text-primary" @click.prevent="addToTable"><i class="fa fa-plus mr-2"></i>Tambah</button>
                    			</CCol>
                    		</CRow>
						</CForm>
						<br>
						<hr class="text-center w-75">
						<br>
						<CRow>
							<CCol sm="12">
								<v-client-table
								:data="order_barang"
								:columns="tableFields"
								:options="tableOptions"
								id="request-table"
								 class="m-4"
								>
									<div slot="aksi" slot-scope="props">
										<button class="btn btn-secondary text-danger m-1"  @click="deleteFromTable(props)"><i class="fa fa-trash"></i></button>				
									</div>
								</v-client-table>
							</CCol>
						</CRow>
					</CCardBody>
					<CCardFooter class="m-4 d-flex justify-content-end">
						<button class="btn btn-light text-primary" :disabled="isEmpty" @click.prevent="orderBarang"><i class="fa fa-cart-plus mr-2"></i> {{label}} </button>
					</CCardFooter>
				</CCard>
			</CCol>
		</CRow>
	</div>
</template>
<script type="text/javascript">
	export default {
		name:'OrderPO',
		data() {
			return {
				labelBarang: 'Pilih Barang',
				isEmpty:true,
				label: 'Selesai',
				errors:[],
				tableFields: ['no', 'kode_barang', 'nama_barang', 'spesifikasi_barang', 'harga_jual', 'qty', 'tax_rate', 'disc', 'total', 'aksi'],
				tableOptions:{
					perPage:10,
					pagination:{chunk:10, dropdown:false, edge:true, nav:'fixed'},
					headings:{	
						no:'No',
						kode_barang: 'Kode Barang',
						nama_barang: 'Nama Barang',
						spesifikasi_barang: 'Spesifikasi',
						harga_jual: 'Harga Jual',
						qty: 'qty',
						tax_rate: 'Pajak %',
						disc: 'Diskon %',
						total: 'Total',
						aksi: 'Aksi'
					},
					sortable: ['no', 'kode_barang', 'nama_barang', 'qty'],
					filterable:['no', 'kode_barang', 'nama_barang', 'spesifikasi_barang', 'harga_jual', 'qty', 'tax_rate', 'disc', 'total'],
					columnsClasses: {
						no:'text-center align-middle',
						kode_barang:'align-middle',
						nama_barang:'align-middle',
						spesifikasi_barang:'align-middle',
						harga_jual:'text-center align-middle',
						qty:'text-center align-middle',
						tax_rate:'text-center align-middle',
						disc:'text-center align-middle',
						total:'text-center align-middle',
						aksi:'text-center align-middle',
					}
					
				},
				barang: [],
				barangs:[],
				validator:{
					harga_jual:null,
					harga_jual_msg:null,
					spesifikasi_barang:null,
					spesifikasi_barang_msg:null,
					qty:null,
					qty_msg:null,
					tanggal:null,
					tanggal_msg:null
				},
				rpo: {
					id_barang:1,
					nama_barang:null,
					spesifikasi_barang:null,
					qty:0,
					tanggal:null,
					harga_jual:null,
					status:1,
					kode_barang: null,
					tax_rate:null,
					diskon:null
				},
				order_barang:[]
			}
		},
		computed: {
			search() {
				return this.barang
			}
		},
		methods: {
			assignNamaBarang(val,i) {
				this.rpo.id_barang = val
				this.rpo.nama_barang = i.target.selectedOptions[0].innerHTML

				for(let i = 0;i < this.barangs.length;i++) {
					if(this.barangs[i].id == val) {
						this.rpo.harga_jual = this.barangs[i].harga_jual
						this.rpo.spesifikasi_barang = this.barangs[i].spesifikasi
						this.rpo.kode_barang = this.barangs[i].kode_barang
						this.rpo.tax_rate = this.barangs[i].tax_rate
						return
					}
				}
			},
			getRequest(url) {
				return new Promise((resolve, reject) => {
					this.$http.get(url, {
	                  headers: {
			            'Authorization': 'bearer ' + localStorage.getItem('token')
			          },
			          redirect:'follow'
			        })
			        .then(response => {
			          resolve(response.data)
			        })
			        .catch(e => {
			          alert('Ada sedikit error saat ambil data!')
			          console.log(e)
			          reject(e)
			          return false
			        })	
				})
		        
		    },
		    deleteFromTable(e) {
		    	// console.log(e.index - 1)
		    	this.order_barang.splice(e.index-1, 1)
		    	// let order_barang = JSON.parse(localStorage.getItem('order_barang'))
		    	// order_barang.splice(e.index)
		    	localStorage.setItem('order_barang', JSON.stringify(this.order_barang))
		    },
		    addToTable() {
		    	this.errors = []
		    	if(!this.rpo.spesifikasi_barang) {
		    		this.validator.spesifikasi_barang = false
	                this.validator.spesifikasi_barang_msg = 'Harap isi Spesifikasi barang'
	                this.errors.push('spesifikasi_barang kosong')	
		    	}

		    	if(!this.rpo.qty) {
		    		this.validator.qty = false
	                this.validator.qty_msg = 'Harap isi qty barang'
	                this.errors.push('qty kosong')
		    	}

		    	if(!this.errors.length){

		    		if(this.order_barang.length > 0) {
		    			for(let i = 0;i < this.order_barang.length;i++) {
		    				if(this.rpo.id_barang == this.order_barang[i].id_barang) {

		    					this.$swal('Barang sudah ditambahkan', 'kamu tidak bisa menambah barang yang sama 2 kali', 'warning')
		    					setTimeout(() => {
		    						this.$swal.close()
		    					}, 1500)
		    					return false
		    				}
			    		}
		    		}
			    		
		    		let arr = {}
		    			arr.id_barang = this.rpo.id_barang
		    			arr.nama_barang = this.rpo.nama_barang
		    			arr.spesifikasi_barang = this.rpo.spesifikasi_barang
		    			arr.qty = this.rpo.qty
		    			arr.no = this.order_barang.length + 1
		    			arr.kode_barang = this.rpo.kode_barang
		    			arr.harga_jual = this.rpo.harga_jual
		    			arr.tax_rate = this.rpo.tax_rate
		    			arr.disc = this.rpo.diskon
		    			arr.total = Math.round(((arr.harga_jual * arr.qty) + arr.tax / 100) - (arr.disc / 100 * (arr.harga_jual * arr.qty)))
		    			arr.id_po = this.$route.params.id
			    		this.order_barang.push(arr)
			    		localStorage.setItem('order_barang', JSON.stringify(this.order_barang))
			    		this.rpo.spesifikasi_barang = ''
			    		this.rpo.qty = ''
			    		this.rpo.kode_barang = ''
			    		this.rpo.harga_jual = ''
			    		this.rpo.tax_rate = ''
			    		this.rpo.diskon = ''
			    		this.labelBarang = 'Pilih Barang'
			    		console.log(this.order_barang)
				        window.scrollTo(0,document.body.scrollHeight)
				        this.isEmpty = false
		    	}
		    	else {
		    		console.log(this.errors)
		    		window.scrollBy({ 
			            top: -500, // could be negative value
			            left: 0, 
			            behavior: 'smooth' 
			          })
			          return false
		    	}
		    	
		    },
		    fetchAll() {
		    	let 
		    	self = this,
		    	arr = []
		    	this.getRequest('https://young-temple-67589.herokuapp.com/api/barang').then((data) => {

		    		if(data.length < 1){
		    			alert('Mohon maaf belum ada barang masuk untuk saat ini, silahkan tambah dulu barangnya yah')
		    			return false
		    		}
		    		else {
		    			this.barangs = data
			    		for(let i = 0;i < data.length;i++) {
			    			let obj = {}
			    			obj.value = data[i].id
			    			obj.label = data[i].nama_barang

			    			self.barang.push(obj)
			    			
			    		}	
		    		}
		    	}) 
		    	.catch(e => console.error(e))
		    },
		    orderBarang() {
		    	this.label = 'Loading...'
		    	
		    	this.$http.post('https://young-temple-67589.herokuapp.com/api/order/barang/po', this.order_barang, {
		    		headers: {
		    			'Authorization': 'bearer ' + localStorage.token
		    		}
		    	})
		    	.then((res) => {
		    		alert(res.data.message)
		    		this.label = 'Selesai'
		    		console.log(res)
		    	})
		    	.then(() => {
		    		this.$router.push({path: '/po'})
		    	})
		    	.catch(e => {
		    		this.label = 'Selesai'
		    		if(e.response) {
		    			for(let er in e.response.data.errmsg) {
		    				alert(e.response.data.errmsg[er][0])
		    			}
		    		}
		    		
		    		console.error(e.response)
		    		return false
		    	})
		    }
		},
		created() {
			this.fetchAll()
			if(localStorage.getItem('order_barang') != null) {
				this.order_barang = JSON.parse(localStorage.getItem('order_barang'))
			}
		}
	}
</script>
<style>
	#request-table .form-inline label {
		display: none;
	}	
	.VuePagination {
		margin-top: 20px;	
		display: flex;
		justify-content: center;
	}
</style>